:root {
  --primary-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  margin: 0;
  padding: 0;
  line-height: 0;
}

.logo__img {
  width: 40px;
  height: 40px;
}

.nav {
  height: 60px;
  line-height: 60px;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  font-size: 12px;
  font-style: normal;
  text-decoration: none;
}

.nav__link {
  color: #000;
  margin-left: 20px;
  padding: 23px 0;
  font-weight: 500;
  text-decoration: none;
}

.nav__link:hover {
  color: var(--primary-color);
}

.is-active {
  color: var(--primary-color);
  margin-left: 0;
  position: relative;
}

nav a.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
